import React, { useState, useEffect } from 'react'
import { Link as RouterLink } from 'react-router-dom'
import { Box, Avatar, Link } from '@chakra-ui/core'

import { User } from '../../api/types'
import { api } from '../../api'

export const Auth: React.FC<{}> = () => {
  const [user, setUser] = useState<User>()

  useEffect(() => {
    DZ.getLoginStatus(async loginStatus => {
      if (loginStatus.authResponse) {
        const { body } = await api.user.fetchMe()

        return body && setUser(body)
      }
    })
  }, [])

  const isAuthorized = !!user

  if (isAuthorized) {
    return (
      <Box>
        <Avatar name={`${user?.firstname} ${user?.lastname}`} />
        <RouterLink to="/my-tracks">My tracks</RouterLink>
        <Link
          onClick={() => {
            DZ.logout()
            setUser(undefined)
          }}
        />
      </Box>
    )
  }

  return (
    <Box>
      <Link
        onClick={() =>
          DZ.login(async loginResponse => {
            if (!loginResponse.authResponse) {
              return
            }

            const { body } = await api.user.fetchMe()
            return body && setUser(body)
          })
        }
      >
        Log in
      </Link>
    </Box>
  )
}
